/* src/Styles.css */

/* Theme for light and dark modes */
[data-theme='light'] {
  --background-color: #f0f0f0;
  --text-color: #333;
  --button-background: #d3d3d3;
  --button-hover-background: #b0b0b0;
}

[data-theme='dark'] {
  --background-color: #333;
  --text-color: #f0f0f0;
  --button-background: #555;
  --button-hover-background: #777;
}

.search-page {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: Arial, sans-serif;
  padding: 20px;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.logo, .earth-logo {
  width: 100px;
  height: auto;
}

.title {
  font-size: 2rem;
  font-weight: bold;
  font-style: italic;
}

hr {
  border: 0;
  height: 1px;
  background-color: var(--text-color);
  margin: 40px 0;
}

.search-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.search-by-brand, .search-by-model {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#search-brand, #search-model {
  padding: 8px;
  font-size: 16px;
  width: 200px;
}

#search-brand-button, #search-model-button {
  padding: 10px 20px;
  font-size: 16px;
  margin-top: 10px;
  background-color: var(--button-background);
  border: 1px solid #ccc;
  border-radius: 5px;
}

#search-brand-button:hover, #search-model-button:hover {
  background-color: var(--button-hover-background);
}

.brand-buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
}

.brand-button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 200px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
}

.brand-logo {
  width: 100px;
  height: auto;
  margin-bottom: 10px;
}

.theme-toggle-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: var(--button-background);
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}

.theme-toggle-button:hover {
  background-color: var(--button-hover-background);
}
