.button-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px; /* Adjust the gap between buttons as needed */
  }
  
  .model-button {
    padding: 10px 20px;
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f5f5f5;
    transition: background-color 0.3s;
  }
  
  .model-button:hover {
    background-color: #e0e0e0;
  }

