.model-details-container {
  text-align: center;
  padding: 20px;
}

.model-heading {
  font-size: 2em;
  margin-bottom: 20px;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.details p {
  margin: 5px 0;
  font-size: 1.2em;
}

.charts-container {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.charts-container .score-chart {
  width: 500px; /* Adjust size as needed */
  height: 500px; /* Adjust size as needed */
}

